<template>
	<view class="forget">
		<view class="uni-flex uni-column">
			<view class="login flex-item">修改密码</view>
			<view class="flex-item">
				<form @submit="formSubmit">
				
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="请输入账号/手机号" />
					</view>
					<view class="uni-form-item uni-column">
						<button class="vCode" form-type="submit">获取验证码</button>
						<input class="uni-input" name="password" placeholder="请输入验证码" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="请输入旧密码" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="请输入新密码" />
					</view>
					<view class="uni-form-item uni-column">
						<input class="uni-input" name="phone" placeholder="请确认新密码" />
					</view>
					<view class="uni-btn-v">
						<button class="submit" form-type="submit">提交</button>
					</view>
				</form>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone:'',
				password:'',
			}
		},
		onLoad() {
	
		},
		methods: {
			formSubmit: function(e) {
				console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
				uni.request({
				    url: 'http://www.aolige.cn/apis/js/a/login', //仅为示例，并非真实接口地址。
					method:'POST',
				    data: {
				        username:this.phone,
				        password:this.password,
				    },
				    header: {
				        'custom-header': 'hello' //自定义请求头信息
				    },
				    success: (res) => {
				        console.log(res.data);
				    }
				});
			},
		}
	}
</script>

<style>
	.forget{
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 20px;
		box-sizing: border-box;
	}
	.login{
		width: 155rpx;
		padding: 0 0 5rpx 0;
		border-bottom: 3px solid #007AFF;
		margin-bottom: 50px;
	}
	.uni-input{
		border-bottom: 1px solid #DCDEE0;
		padding: 20rpx 0;
		margin: 40rpx 0 ;
	}
	.vCode{
		position: absolute;
		right: 20px;
		font-size: 16px;
		background-color: #007AFF;
		color: #FFFFFF;
	}
	.submit{
		margin-top: 80rpx;
		background-color: #007AFF;
		color: #FFFFFF;
	}
</style>
